import {inject} from 'aurelia-framework';
import Fixtures from './fixtures';
import {TotalUpdate, LoginStatus } from './messages';
import {EventAggregator} from 'aurelia-event-aggregator';

@inject(Fixtures, EventAggregator)

export default class DonationService {


  donations = [];
  methods = [];
  candidates = [];
  users = [];
  total = 0;

  constructor(data, ea) {
    this.donations = data.donations;
    this.candidates = data.candidates;
    this.methods = data.methods;
    this.users = data.users;
    this.ea = ea;
  }

  donate(amount, method, candidate) {
    let donation = {
      amount: amount,
      method: method,
      candidate: candidate
    };
    this.donations.push(donation);
    console.log(amount + ' donated to ' + candidate.firstName + ' ' + candidate.lastName + ': ' + method);
    this.total = this.total + parseInt(amount, 10);
    console.log('Total so far' + this.total);
    this.ea.publish(new TotalUpdate(this.total));
  }

  newCandidate(firstName, lastName, office) {
    let cand = {
      firstName: firstName,
      lastName: lastName,
      office: office
    };
    this.candidates.push(cand);
    console.log(firstName, lastName, office);
  }

  login(email, password) {
    const status = {
      success: false,
      message: ''
    };
    for (let i = 0; i < this.users.length; i++) {
      if ( email === this.users[i].email && password === this.users[i].password) {
        console.log('authenticate ' + this.users[i].email)
        status.success = true;
        status.message = 'logged in';
        //return status;
        this.ea.publish(new LoginStatus(status));
      }
    }
    return false;
  }

  register(firstName, lastName, email, password) {
    const newUser = {
      firstName: firstName,
      lastName: lastName,
      email: email,
      password: password
    };
    this.users[email] = newUser;
  }

  logout() {
    const status = {
      success: false,
      message: ''
    };
    this.ea.publish(new LoginStatus(new LoginStatus(status)));
  }
}

